import { Column } from '@codecademy/gamut/src';
// The normal import would be '@storybook/addon-links/react'
// but it is throwing a typescript error in the current alpha
import LinkTo from '@storybook/addon-links/dist/react/components/link';
import React from 'react';

import { Section } from '../../Templating';
import styles from './styles.module.scss';

export type ExampleShape = {
  name: string;
  kind: string;
};

export type AboutCellProps = {
  emoji: string;
  examples?: ExampleShape[];
  kind: Section;
  story?: string;
  title?: string;
  category?: string;
};

export const AboutCell: React.FC<AboutCellProps> = ({
  children,
  emoji,
  examples,
  kind,
  category,
  story = 'About',
  title = kind || category,
}) => {
  const kindLink = [category, kind].filter(Boolean).join('|');
  return (
    <Column className={styles.aboutCell} size={{ xs: 12, sm: 6, md: 4 }}>
      <h2 className={styles.heading}>
        <LinkTo kind={kindLink} story={story}>
          <span role="presentation">{emoji}</span> {title}
        </LinkTo>
      </h2>
      <p className={styles.children}>{children}</p>
      {examples && (
        <div className={styles.examples}>
          Examples:{' '}
          {examples.map((example, i) => {
            const exampleLink = [category, example.kind]
              .filter(Boolean)
              .join('|');
            return (
              <React.Fragment key={example.name}>
                <LinkTo kind={exampleLink} story={example.name}>
                  {example.name || example}
                </LinkTo>
                {i !== examples.length - 1 && ', '}
              </React.Fragment>
            );
          })}
        </div>
      )}
    </Column>
  );
};

export default AboutCell;
try {
    // @ts-ignore
    AboutCell.displayName = "AboutCell";
    // @ts-ignore
    AboutCell.__docgenInfo = { "description": "", "displayName": "AboutCell", "props": { "emoji": { "defaultValue": null, "description": "", "name": "emoji", "required": true, "type": { "name": "string" } }, "examples": { "defaultValue": null, "description": "", "name": "examples", "required": false, "type": { "name": "ExampleShape[]" } }, "kind": { "defaultValue": null, "description": "", "name": "kind", "required": true, "type": { "name": "enum", "value": [{ "value": "\"Atoms\"" }, { "value": "\"Brand\"" }, { "value": "\"Foundations\"" }, { "value": "\"Molecules\"" }, { "value": "\"Organisms\"" }] } }, "story": { "defaultValue": { value: "About" }, "description": "", "name": "story", "required": false, "type": { "name": "string" } }, "title": { "defaultValue": { value: "kind || category" }, "description": "", "name": "title", "required": false, "type": { "name": "string" } }, "category": { "defaultValue": null, "description": "", "name": "category", "required": false, "type": { "name": "string" } } } };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["stories/Abouts/AboutCell/index.tsx#AboutCell"] = { docgenInfo: AboutCell.__docgenInfo, name: "AboutCell", path: "stories/Abouts/AboutCell/index.tsx#AboutCell" };
}
catch (__react_docgen_typescript_loader_error) { }